import React from "react";
import { connect } from "react-redux";
import classNames from "../../containers/NumberBoard/Board.module.css";

const Row = (props) => {
  let classes;
  if (props.grid) {
    classes = `${classNames.carry} ${classNames.c1} ${classNames.brc}`;
  } else {
    classes = `${classNames.carry} ${classNames.c1}`;
  }
  return (
    <>
      <div className={`${classNames.row}`}>
        <div className={classes}>
          <h2>{null}</h2>
        </div>
        <div className={classes}>
          <h2>{null}</h2>
        </div>
        <div className={classes}>
          <h2>{null}</h2>
        </div>
        <div className={classes}>
          {props.numbers[0].map((c, i) => {
            if (c) {
              return (
                <h2 style={{ backgroundColor: "#ffffc0" }} key={i}>
                  {c}
                </h2>
              );
            } else return null;
          })}
        </div>
        <div className={classes}>
          {props.numbers[1].map((c, i) => {
            if (c) {
              return (
                <h2 style={{ backgroundColor: "#ffffc0" }} key={i}>
                  {c}
                </h2>
              );
            } else return null;
          })}
        </div>
        <div className={classes}>
          <h2>{null}</h2>
        </div>
      </div>
    </>
  );
};

const mapStatetoProps = (state) => {
  return {
    grid: state.menu.grid,
  };
};

export default connect(mapStatetoProps)(Row);
